import React from 'react';
import PropTypes from 'prop-types';

import { Button } from 'semantic-ui-react';

import './HistoryItem.css';

import BitDisplay from './BitDisplay';

import { displayTypes } from '../actions/globals';
import { displayNumber } from '../util';

function opNameToSymbol(op) {
  switch (op) {
    case 'and': return '&';
    case 'or' : return '|';
    case 'xor': return '^';
    default: return 'UNKNOWN';
  }
}

const HistoryItem = (props) => {
  const size = props.large ? 'medium' : 'small';
  const { op, res, A, B } = props.item;
  const { display, setRegister, remove } = props;
  return (
    <div className="HistoryItem">
      <div>
        <Button size="mini" onClick={remove}>&times;</Button>
        <Button.Group size="mini">
          <Button onClick={() => setRegister('A', res)}>A</Button>
          <Button onClick={() => setRegister('B', res)}>B</Button>
        </Button.Group>
      </div>
      <BitDisplay number={res} display={display} size={size} />
      <div className="HistoryItem__Origin">
        { `(${displayNumber(A, display)} ${opNameToSymbol(op)} ${displayNumber(B, display)})` }
      </div>
    </div>
  );  
}

HistoryItem.propTypes = {
  large: PropTypes.bool,
  item: PropTypes.shape({
    op: PropTypes.oneOf(['and', 'or', 'xor']).isRequired,
    res: PropTypes.number.isRequired,
    A: PropTypes.number.isRequired,
    B: PropTypes.isRequired,
  }).isRequired,
  display: PropTypes.oneOf(Object.values(displayTypes)),

  remove: PropTypes.func.isRequired,
  setRegister: PropTypes.func.isRequired,
};

export default HistoryItem;




