import React from 'react';
import PropTypes from 'prop-types';

import './HistoryViewer.css';

import { Button } from 'semantic-ui-react';

import HistoryItem from './HistoryItem';
import { displayTypes } from '../actions/globals';

const HistoryViewer = (props) => {
  console.log(props);
  return (
    <div className="HistoryViewer"> 
      <div className="HistoryViewer__Title">
        <h2>History</h2>
        <Button size="mini" className="Button" onClick={props.clear}>Clear</Button>
      </div>
      <ul>
        { props.history.map((item, i) => <HistoryItem
                                          key={i}
                                          index={i}
                                          large={i===0}
                                          item={item}
                                          display={props.display}
                                          setRegister={props.registerSet}
                                          remove={() => props.remove(i)}
                                          /> )}
      </ul>
    </div>
  );
};

HistoryViewer.propTypes = {
  history: PropTypes.array.isRequired,
  display: PropTypes.oneOf(Object.values(displayTypes)),
  clear: PropTypes.func.isRequired,

  remove: PropTypes.func.isRequired,
  registerSet: PropTypes.func.isRequired,
};

export default HistoryViewer;


