export const HISTORY_ADD = 'HISTORY_ADD';
export const HISTORY_CLEAR = 'HISTORY_CLEAR';
export const HISTORY_REMOVE = 'HISTORY_REMOVE';

export function historyAdd(item) {
  return {
    type: HISTORY_ADD,
    item,
  };
}

export function historyClear() {
  return {
    type: HISTORY_CLEAR,
  };
}

export function historyRemove(index) {
  return {
    type: HISTORY_REMOVE,
    index,
  };
}


