import {
  HISTORY_ADD,
  HISTORY_CLEAR,
  HISTORY_REMOVE,
} from '../actions/history';

const initialState = [];

export default function historyReducer(state = initialState, action) {
  switch (action.type) {
    case HISTORY_ADD:
      return [action.item].concat(state);
    case HISTORY_REMOVE:
      return state.filter((_, i) => i !== action.index);
    case HISTORY_CLEAR:
      return initialState;
    default:
      return state;
  }
}


