export const REGISTERS_SET = 'REGISTERS_SET';
export const REGISTERS_SETBIT = 'REGISTERS_SETBIT';
export const REGISTERS_NOT = 'REGISTERS_NOT';
export const REGISTERS_LSHIFT = 'REGISTERS_LSHIFT';
export const REGISTERS_RSHIFT = 'REGISTERS_RSHIFT';

export function registerSet(register, value) {
  return {
    type: REGISTERS_SET,
    register,
    value,
  };
}

export function registerSetbit(register, n) {
  return {
    type: REGISTERS_SETBIT,
    register,
    n,
  };
}

export function registerNot(register) {
  return {
    type: REGISTERS_NOT,
    register,
  };
}

export function registerLshift(register) {
  return {
    type: REGISTERS_LSHIFT,
    register,
  };
}

export function registerRshift(register) {
  return {
    type: REGISTERS_LSHIFT,
    register,
  };
}


