import React from 'react';

import { Container } from 'semantic-ui-react';

import ControlsContainer from '../containers/ControlsContainer';
import RegisterDisplayContainer from '../containers/RegisterDisplayContainer';
import OperationsContainer from '../containers/OperationsContainer';
import HistoryViewerContainer from '../containers/HistoryViewerContainer';

const App = () => (
  <div className="Bitwise">
    <Container>
      <ControlsContainer />
      <RegisterDisplayContainer register="A" />
      <RegisterDisplayContainer register="B" />
      <OperationsContainer />
      <HistoryViewerContainer />
    </Container>
  </div>
);

export default App;


