// TODO: check prop number is in range [0, 0xffff]
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Button } from 'semantic-ui-react';

import './RegisterDisplay.css';

import BitDisplay from './BitDisplay';
import { displayTypes } from '../actions/globals';

const buttonSize = 'mini';

class RegisterDisplay extends Component {
  render() {
    // const { number } = this.state;
    const { display, register, set, setbit, not, lshift, rshift, } = this.props;
    const number = this.props.registers[register];

    const onBitClick = i => setbit(register, i);

    return (
      <div className="RegisterDisplay">
        <div className="RegisterDisplay__TopRow">
          <div className="RegisterDisplay__Unaries">
            <Button size={buttonSize} onClick={() => not(register)}>not</Button>
            <Button size={buttonSize} onClick={() => set(register, 0)}>clear</Button>
            <Button.Group size={buttonSize}>
              <Button onClick={() => lshift(register)}>{'<<'}</Button>
              <Button onClick={() => rshift(register)}>{'>>'}</Button>               
            </Button.Group>
            {/* <button className="Button RegisterDisplay__Unaries__Not" onClick={() => not(register)}>Not</button>
            <button className="Button RegisterDisplay__Unaries__Shift" onClick={() => lshift(register)}>{'<<'}</button>
            <button className="Button RegisterDisplay__Unaries__Shift" onClick={() => rshift(register)}>{'>>'}</button> */}
          </div>
        </div>
        <div className="RegisterDisplay__BottomRow">
          <BitDisplay number={number} onBitClick={onBitClick} display={display} size="large" />
        </div>
      </div>
    );
  }
  
  static propTypes = {
    display: PropTypes.oneOf(Object.values(displayTypes)).isRequired,
    register: PropTypes.oneOf(['A', 'B']).isRequired,

    set: PropTypes.func.isRequired,
    setbit: PropTypes.func.isRequired,
    not: PropTypes.func.isRequired,
    lshift: PropTypes.func.isRequired,
    rshift: PropTypes.func.isRequired,
  };
}

export default RegisterDisplay;


