import React from 'react';
import PropTypes from 'prop-types';

import { Button } from 'semantic-ui-react';

import { displayTypes } from '../actions/globals';

const Controls = (props) => {
  const { display, setGlobalDisplay } = props;
  return (
    <div className="Controls">
      <Button.Group size="mini">
        <Button disabled={display === 'dec'} onClick={() => setGlobalDisplay('dec')}>Dec</Button>
        <Button disabled={display === 'oct'} onClick={() => setGlobalDisplay('oct')}>Oct</Button>
        <Button disabled={display === 'hex'} onClick={() => setGlobalDisplay('hex')}>Hex</Button>
      </Button.Group>
    </div>
  );
};

Controls.propTypes = {
  display: PropTypes.oneOf(Object.values(displayTypes)),
  setGlobalDisplay: PropTypes.func.isRequired,
};

export default Controls;


