import {
  REGISTERS_SET,
  REGISTERS_SETBIT,
  REGISTERS_NOT,
  REGISTERS_LSHIFT,
  REGISTERS_RSHIFT,
} from '../actions/registers';

export const initialState = {
  A: 0,
  B: 0,
};

const mask = 0xffff;

function registersReducer(state = initialState, action) {
  const { register, type } = action
  switch (type) {
    case REGISTERS_SET:
      return {...state, [register]: action.value };
    case REGISTERS_SETBIT: {
      const mask = 1 << action.n;
      const value = state[register] ^ mask;
      return {...state, [register]: value};
    }
    case REGISTERS_NOT: {
      const value = state[register] ^ mask;
      return {...state, [register]: value };
    }
    case REGISTERS_LSHIFT: {
      const value = (state[register] << 1) & mask;
      return {...state, [register]: value };
    }
    case REGISTERS_RSHIFT:
      const value = (state[register] >> 1) & mask;
      return {...state, [register]: value };
    default:
      return state;      
  }
}

export default registersReducer;


