import React from 'react';
import PropTypes from 'prop-types';

import './BitDisplay.css';

import { displayTypes } from '../actions/globals';
import { displayNumber } from '../util';

/**
 * getBits returns an array of bools
 * with length 16. Each bool corresponds
 * to the corresponding bit value of
 * number. Therefore, only bits 0-15 (msb)
 * are considered
 * @param {number} number 
 */
function getBits(number) {
  return new Array(16)
    .fill(0)
    .map((_, i) => (number & (1<<i)) !== 0)
}

function renderBit(bit, i, clickHandler) {
  return (
    <div className="BitDisplay__Bit" key={i} onClick={clickHandler}>
      {bit ? '1' : '0'}
    </div>
  )
}

const BitDisplay = (props) => {
  const sizeState = `BitDisplay--${props.size || 'small'}`;
  return (
    <div className={`BitDisplay ${sizeState}`}>
      <div className="BitDisplay__Bits">
        {
          getBits(props.number)
            .map((bit, i) => renderBit(bit, i, () => props.onBitClick && props.onBitClick(i)))
            .sort(() => 1) // studid immutable way to reverse...
        }
      </div>
      <div className="BitDisplay__Value">
        { displayNumber(props.number, props.display) }
      </div>
    </div>
  );
};

BitDisplay.propTypes = {
  size: PropTypes.oneOf(['small', 'medium', 'large']),
  number: PropTypes.number.isRequired,
  display: PropTypes.oneOf(Object.values(displayTypes)),
  onBitClick: PropTypes.func,
};

export default BitDisplay;




