export const displayTypes = {
  DECIMAL: 'dec',
  OCTAL: 'oct',
  HEXADECIMAL: 'hex',
};

export const GLOBALS_DISPLAY_SET = 'GLOBALS_DISPLAY_SET';

export function setGlobalDisplay(value) {
  return {
    type: GLOBALS_DISPLAY_SET,
    value,
  };
}


