import { connect } from 'react-redux';

import { historyClear, historyRemove } from '../actions/history';
import { registerSet } from '../actions/registers';

import HistoryViewer from '../components/HistoryViewer';

const mapStateToProps = (state) => ({
  history: state.history,
  display: state.globals.display,
});

const mapDispatchToProps = (dispatch) => ({
  clear: () => dispatch(historyClear()),
  remove: index => dispatch(historyRemove(index)),
  registerSet: (register, n) => dispatch(registerSet(register, n)),
});

export default connect(mapStateToProps, mapDispatchToProps)(HistoryViewer);


