import { GLOBALS_DISPLAY_SET } from '../actions/globals';

const initialState = {
  display: 'hex',
};

export default function globalsReducer(state = initialState, action) {
  switch (action.type) {
    case GLOBALS_DISPLAY_SET:
      return {...state, display: action.value };
    default:
      return state;
  }
}


