import { connect } from 'react-redux';

import {
  registerSet,
  registerSetbit,
  registerNot,
  registerLshift,
  registerRshift,
} from '../actions/registers';

import RegisterDisplay from '../components/RegisterDisplay';

const mapStateToProps = state => ({
  registers: state.registers,
  display: state.globals.display,
});

const mapDispatchToProps = dispatch => ({
  set: (register, n) => dispatch(registerSet(register, n)),
  setbit: (register, n) => dispatch(registerSetbit(register, n)),
  not: register => dispatch(registerNot(register)),
  lshift: register => dispatch(registerLshift(register)),
  rshift: register => dispatch(registerRshift(register)),
});

export default connect(mapStateToProps, mapDispatchToProps)(RegisterDisplay);


