import React from 'react';
import PropTypes from 'prop-types';

import { Button } from 'semantic-ui-react';

import './Operations.css';

function xor(register) {
  const {A, B} = register;
  return {
    op: 'xor',
    res: A ^ B,
    A, B,
  };
}

function or(register) {
  const {A, B} = register;
  return {
    op: 'or',
    res: A | B,
    A, B,
  };
}

function and(register) {
  const {A, B} = register;
  return {
    op: 'and',
    res: A & B,
    A, B,
  };
}

const buttonSize = 'large';

const Operations = (props) => (
  <div class="Operations">
    <Button.Group>
      <Button size={buttonSize} className="Button" onClick={() => props.historyAdd(and(props.registers))}>and</Button>
      <Button size={buttonSize} className="Button" onClick={() => props.historyAdd(or(props.registers))}>or</Button>
      <Button size={buttonSize} className="Button" onClick={() => props.historyAdd(xor(props.registers))}>xor</Button>
    </Button.Group>
  </div>
);

Operations.propTypes = {
  registers: PropTypes.shape({
    A: PropTypes.number.isRequired,
    B: PropTypes.number.isRequired,
  }).isRequired,

  historyAdd: PropTypes.func.isRequired,
};

export default Operations;


